import uniPassword from '../../../components/uni-password/uni-password.vue'
import uniKeyboard from '../../../components/uni-keyboard.vue'
import {
	appUpdInfo
} from '../../../api/auth.js'
import {
	putUserInfo
} from '../../../setting/putUserInfo.js'
export default {
	components: {
		uniKeyboard,
		uniPassword
	},
	data() {
		return {
			showKeyboard: false
		}
	},
	methods: {
		onInput(e) {
			//e.cancel = true;
			// console.log(e)
		},
		onConfirm(e) {
			let password = e.value;
			// console.log(password)
		},
		onAutoCheck(e) {
			// console.log(e)
			let password = e.password.join('')
			// 自动验证
			let params = {
				paypassword: password
			}
			appUpdInfo(params).then(data => {
				console.log(data)
				if (data.errno == 200) {
					uni.showToast({
						title: '设置成功'
					});
					putUserInfo(data).then(data => {
						console.log(data)
					}).catch(err => {
						console.log(err)
					})
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						});
					}, 500);
				}
			}).catch(err => {
				console.log(err)
			})
		},
		goGetPwd() {
			uni.navigateTo({
				url: '../getPwd/getPwd'
			})
		}
	},
}
